}


.show-img {
    position: relative;
    width: 100%;
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.show-img h2 {
    margin: 3vw 0;
    text-transform: capitalize;
    font-family: 'Fredoka One', cursive;
    font-size: 3vw;
}

.show-img ul {
    list-style: none;
    position: relative;
    width: 97%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
}

.show-img ul li {
    position: relative;
    width: 22%;
    height: 30vh;
    margin: calc(1px + 1vw) 10px;
    overflow: hidden;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.555) 0px 0px 10px;
}

.show-img ul li p {
    position: relative;
    width: 100%;
    height: 20%;
    padding: 0 5px;
    font-size: calc(5px + 1vw);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-transform: capitalize;
    background-color: white;
    overflow: hidden;
    line-height: 6vh;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    vertical-align: middle;
    font-family: 'Architects Daughter', cursive;
}

.show-img ul li img {
    width: 100%;
    height: 80%;
    object-fit: cover;
    vertical-align: middle;
    transition: all 1s;
}

.show-img ul li:hover img{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.popup-container {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 999;
    background-color: rgba(10, 10, 10, 0.65);
    overflow: hidden;   
}

.popup-container img {
    width: 60%;
    height: 75%;
    object-fit: cover;
}

.popup-container p {
    margin: 40px 0 0 0;
    font-size: calc(12px + 2vmin);
    color : white;
    text-transform: capitalize;
    width: 45%;
    height: 40px;
    font-family: 'Architects Daughter', cursive;
}

.popup-container button {
    position: absolute;
    top: 1%;
    right: 1%;
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    cursor: pointer;
    border-radius: 50%;
}

@font-face {
    font-family: "FontAwesome 5 Pro Regular";
    src: url(/SnapShot/static/media/fa-regular-400.af399b5e.woff);
}

.popup-container button::after {
    content: '\f00d';
    font-family: "FontAwesome 5 Pro Regular";
    width: 100%;
    font-size: 20px;
    color: white;
    background-color: none;
}

.popup-container button:hover {
    background: rgb(68, 68, 68);
}


.nav-btn {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 0 10vw;
    margin: 3vw 0;
}

.button:last-child:after  {
    content:'\f105';
    font-family: "FontAwesome";
    position: relative;
    text-align: center;
}

.button:first-child:before{
    content:"\f104";
    font-family: "FontAwesome";
    position: relative;
    text-align: center;
}

.button:last-child:hover, .button:first-child:hover {
    background-color: rgb(68, 68, 68);
    font-style: italic;
}

@media only screen and ( max-width:1070px ) {
    .show-img ul li {
